import Header from '../components/Header';
import axios from 'axios';

const Index =  ({products}) =>{
    return(
        <div>
        <Header/>
        {products.slice(1,10).map(product =>{
            return (
<>
                <div style={{marginTop:'10px', background:'black'}}>{product.title}</div>
                <div style={{marginTop:'10px', background:'black'}}>{product.body}</div>
                </>
            )
            })}
    </div>
    )
}

Index.getInitialProps = async function(){
     let products = await axios.get('https://jsonplaceholder.typicode.com/posts')
    return {products: products.data}
}

export default Index;